import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {actionTypes} from '@servicenow/ui-core';
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-button';
import '@servicenow/now-tooltip';
import {
	schedule,
	unschedule,
	createTooltipActionHandler
} from '@servicenow/now-tooltip';
import '@servicenow/now-dropdown';
import styles from './_now-pagination-control.scss';
import {t} from 'sn-translate';
import focusBehavior from '@servicenow/behavior-focus';
const {COMPONENT_DISCONNECTED} = actionTypes;

const showTooltip = (updateState, page, key) => {
	const PAGE_NUM = key + 1;
	updateState({
		path: 'currentTooltip',
		operation: 'set',
		value: {ref: page, content: t('Page {0}', PAGE_NUM), opened: true}
	});
};

const hideTooltip = (updateState) => {
	updateState({
		path: 'currentTooltip',
		operation: 'set',
		value: {ref: null, content: '', opened: false}
	});
};

const renderPage = ({key, selected, dispatch, updateState, componentId}) => {
	const PAGE_NUM = key + 1;
	return (
		<button
			key={key}
			type="button"
			class={{
				'now-pagination-control-button': true,
				'is-selected': selected
			}}
			aria-label={t('Page {0}', PAGE_NUM)}
			aria-current={selected ? 'page' : 'false'}
			data-itemid={key}
			on-click={() => {
				dispatch(() => {
					return {
						type: 'NOW_PAGINATION_CONTROL#SELECTED_PAGE_SET',
						payload: {
							value: key
						}
					};
				});
			}}
			on-mouseenter={(e) => {
				const page = e.target;
				schedule(componentId, () => showTooltip(updateState, page, key));
			}}
			on-focusin={(e) => {
				const page = e.target;
				schedule(componentId, () => showTooltip(updateState, page, key));
			}}
			on-mouseleave={() => {
				schedule(componentId, () => hideTooltip(updateState));
			}}
			on-focusout={() => {
				schedule(componentId, () => hideTooltip(updateState));
			}}>
			<span className="now-line-height-crop" data-testid="page-number-label">
				{PAGE_NUM}
			</span>
		</button>
	);
};

const renderRange = (total, selectedPageSize, selectedPage, rangeLabel) => {
	let rangeStart = selectedPage * selectedPageSize + 1;
	let rangeEnd, label, ariaLabel;
	if (
		selectedPageSize > total ||
		(selectedPage + 1) * selectedPageSize > total
	) {
		rangeEnd = total;
	} else {
		rangeEnd = (selectedPage + 1) * selectedPageSize;
	}

	if (rangeLabel) {
		label = rangeLabel;
		ariaLabel = rangeLabel;
	} else if (total === 0) {
		label = t('Showing 0 records');
		ariaLabel = t('Showing 0 records');
	} else {
		label = t('Showing {0}-{1} of {2}', rangeStart, rangeEnd, total);
		ariaLabel = t(
			'Showing {0} through {1} of {2}',
			rangeStart,
			rangeEnd,
			total
		);
	}

	return (
		<p className="now-m-block--0 now-m-inline-start--xl" aria-label={ariaLabel}>
			<span
				className="now-line-height-crop"
				data-testid="page-range-label"
				aria-hidden="true">
				{label}
			</span>
		</p>
	);
};

const view = (state, {dispatch, updateState}) => {
	const {
		total,
		selectedPage,
		pageSizes,
		hideGoToFirst,
		hideGoToPrevious,
		hideGoToNext,
		hideGoToLast,
		hidePageNumbers,
		hidePageSizeControl,
		hideRange,
		selectedPageSize,
		visiblePages,
		pagesOverride,
		rangeLabel,
		pageSizeLabel
	} = state.properties;
	const {componentId} = state;

	const totalPages = pagesOverride || Math.ceil(total / selectedPageSize);

	let isFirst = total === 0 || selectedPage === 0;

	let isLast = total === 0 || selectedPage === totalPages - 1;

	const halfVisiblePages = Math.floor(visiblePages / 2);

	let firstVisiblePageIndex;
	// don't shift too far left
	if (selectedPage - halfVisiblePages <= 0 || totalPages < visiblePages) {
		firstVisiblePageIndex = 0;
	}
	// don't shift too far right
	else if (selectedPage > totalPages - visiblePages + halfVisiblePages) {
		firstVisiblePageIndex = totalPages - visiblePages;
	} else {
		firstVisiblePageIndex = selectedPage - halfVisiblePages;
	}

	const lastVisiblePageIndex = Math.min(
		firstVisiblePageIndex + visiblePages - 1,
		totalPages - 1
	);

	const renderGoToFirst = () => {
		return (
			<now-button-iconic
				className="now-pagination-control-button-iconic"
				data-testid="go-to-first"
				icon="arrow-start-most-outline"
				bare
				variant="tertiary"
				tooltipContent={t('Go to first page')}
				disabled={isFirst}
				append-to-payload={{
					page: 0
				}}
			/>
		);
	};

	const renderGoToPrevious = () => {
		return (
			<now-button-iconic
				className="now-pagination-control-button-iconic"
				data-testid="go-to-previous"
				icon="arrow-start-outline"
				bare
				variant="tertiary"
				tooltipContent={t('Go to previous page')}
				disabled={isFirst}
				append-to-payload={{
					page: selectedPage - 1
				}}
			/>
		);
	};

	const renderPageButtons = (selectedPage, dispatch) => {
		let pageNumbers = [];

		for (let i = firstVisiblePageIndex; i < lastVisiblePageIndex + 1; i++) {
			pageNumbers.push(
				renderPage({
					key: i,
					selected: i === selectedPage,
					dispatch,
					updateState,
					componentId
				})
			);
		}

		return (
			<Fragment>
				{pageNumbers}
				<now-tooltip
					id="tooltip"
					manage-opened
					opened={state.currentTooltip.opened}
					target-ref={state.currentTooltip.ref}
					content={state.currentTooltip.content}
					delay={{show: 0, hide: 0}}
				/>
			</Fragment>
		);
	};

	const renderGoToNext = () => {
		return (
			<now-button-iconic
				className="now-pagination-control-button-iconic"
				data-testid="go-to-next"
				icon="arrow-end-outline"
				bare
				variant="tertiary"
				tooltipContent={t('Go to next page')}
				disabled={isLast}
				append-to-payload={{
					page: selectedPage + 1
				}}
			/>
		);
	};

	const renderGoToLast = () => {
		return (
			<now-button-iconic
				className="now-pagination-control-button-iconic"
				data-testid="go-to-last"
				icon="arrow-end-most-outline"
				bare
				variant="tertiary"
				tooltipContent={t('Go to last page')}
				disabled={isLast}
				append-to-payload={{
					page: totalPages - 1
				}}
			/>
		);
	};

	const renderPageSizeControl = (pageSizes, selectedPageSize) => {
		const pageSizeOptions = pageSizes.map((size) => {
			return {id: `${size}`, label: `${size}`};
		});

		return (
			<div className="now-align now-m-inline-end--xl -block-center">
				<p
					className="now-m-inline-end--sm now-m-block--0"
					data-testid="page-size-control-label">
					<span className="now-line-height-crop">
						{pageSizeLabel || t('Records per page')}
					</span>
				</p>

				<now-dropdown
					data-testid="page-size-options"
					items={pageSizeOptions}
					className="now-pagination-control-dropdown"
					variant="secondary"
					placeholder={pageSizes[0]}
					selected-items={[String(selectedPageSize)]}
					show-padding
					panel-fit-props={{
						position: [
							'bottom-end top-end',
							'top-end bottom-end',
							'top-start top-end',
							'bottom-start bottom-end'
						]
					}}
					bare
					config-aria={{
						trigger: {'aria-label': pageSizeLabel || 'Records per page'}
					}}
				/>
			</div>
		);
	};

	return (
		<nav
			class={{
				'now-pagination-control': true,
				'only-prev-next':
					hideRange &&
					hideGoToFirst &&
					hideGoToLast &&
					hidePageNumbers &&
					hidePageSizeControl
			}}
			aria-label={t('Pagination')}>
			{!hideRange
				? renderRange(total, selectedPageSize, selectedPage, rangeLabel)
				: undefined}
			<div className="now-pagination-control-pages">
				{!hideGoToFirst ? renderGoToFirst() : undefined}
				{!hideGoToPrevious ? renderGoToPrevious() : undefined}
				{!hidePageNumbers
					? renderPageButtons(
							selectedPage,
							dispatch,
							updateState,
							componentId,
							firstVisiblePageIndex,
							lastVisiblePageIndex
					  )
					: undefined}
				{!hideGoToNext ? renderGoToNext() : undefined}
				{!hideGoToLast ? renderGoToLast() : undefined}
			</div>
			{!hidePageSizeControl
				? renderPageSizeControl(pageSizes, selectedPageSize)
				: undefined}
		</nav>
	);
};

/**
 * Enables users to navigate through a large number of items distributed across pages.
 *
 * ```jsx
 * <now-pagination-control total={10}></now-pagination-control>
 * ```
 *
 * @seismicElement now-pagination-control
 * @summary Divides a large set of content into consolidated pages for the user to navigate between.
 * @uib.label Pagination control
 * @uib.icon pagination-control
 * @uib.description Divides a large set of content into consolidated pages for the user to navigate between.
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties pageSizes, selectedPage, selectedPageSize, total, visiblePages, hideGoToFirst, hideGoToLast, hideGoToNext, hideGoToPrevious, hidePageNumbers, hidePageSizeControl, hideRange, pagesOverride, pageSizeLabel, rangeLabel
 * @uib.actions NOW_PAGINATION_CONTROL#SELECTED_PAGE_SET
 */

createEnhancedElement('now-pagination-control', {
	properties: {
		/**
		 * Visible pages to configure.
		 * @type {integer}
		 * @uib.description Changes the number of pages that can be directly selected between arrows
		 */
		visiblePages: {schema: {type: 'integer', minimum: 0}, default: 5},
		/**
		 * Total number of records.
		 * @type {integer}
		 * @uib.label Total records
		 * @uib.description Total number of records
		 * @uib.defaultValue 100
		 */
		total: {
			schema: {type: 'integer', minimum: 0}
		},
		/**
		 * Initial page index to render.
		 * @type {integer}
		 * @uib.label Selected page index
		 * @uib.description Initial page index to render
		 */
		selectedPage: {
			manageable: true,
			schema: {type: 'integer', minimum: 0},
			default: 0
		},
		/**
		 * When true, hides text displaying the current range of the total number of records.
		 * @type {boolean}
		 * @uib.description Select to hide text displaying the current range of the total number of records
		 */
		hideRange: {schema: {type: 'boolean'}},
		/**
		 * When true, hides the "Go to first page" button.
		 * @type {boolean}
		 * @uib.label Hide first page button
		 * @uib.description Select to hide the "Go to first page" button
		 */
		hideGoToFirst: {schema: {type: 'boolean'}},
		/**
		 * When true, hides the "Go to previous page" button.
		 * @type {boolean}
		 * @uib.label Hide previous page button
		 * @uib.description Select to hide the "Go to previous page" button
		 */
		hideGoToPrevious: {schema: {type: 'boolean'}},
		/**
		 * When true, hides the "Go to next page" button.
		 * @type {boolean}
		 * @uib.label Hide next page button
		 * @uib.description Select to hide the "go to next page" button
		 */
		hideGoToNext: {schema: {type: 'boolean'}},
		/**
		 * When true, hides the "Go to last page" button.
		 * @type {boolean}
		 * @uib.label Hide last page button
		 * @uib.description Select to hide the "Go to last page" button
		 */
		hideGoToLast: {schema: {type: 'boolean'}},
		/**
		 * When true, hides page number buttons.
		 * @type {boolean}
		 * @uib.label Hide page numbers
		 * @uib.description Select to hide page number buttons
		 */
		hidePageNumbers: {schema: {type: 'boolean'}},
		/**
		 * When true, hides the page size control.
		 * This control allows the user to set a page size preference
		 * from a set of defined page size thresholds.
		 * @type {boolean}
		 * @uib.label Hide page size
		 * @uib.description Select to hide the page size control that allows the user to set a page size preference from a set of defined page size thresholds
		 */
		hidePageSizeControl: {schema: {type: 'boolean'}},
		/**
		 * Page size choices in the page size control.
		 * Default values: 10, 20, 50, 100.
		 * @type {array}
		 * @uib.description Page size choices in the page size control
		 */
		pageSizes: {
			required: true,
			schema: {type: 'array', items: {type: 'integer'}},
			default: [10, 20, 50, 100]
		},
		/**
		 * Page size choice that is selected from the dropdown
		 * @type {integer}
		 * @uib.label Page size
		 * @uib.description Page size choice that is currently selected from the dropdown
		 */
		selectedPageSize: {
			manageable: true,
			schema: {type: 'integer'},
			default: 10
		},
		/**
		 * Forces the component to display a specific number of pages.
		 * If you do not set this property, the number of pages is calculated automatically based on the
		 * `total` and the `selectedPageSize` properties.
		 * @type {integer}
		 */
		pagesOverride: {schema: {type: 'integer'}},
		/**
		 * Custom label to display next to the page size selector.
		 * @type {string}
		 * @uib.translatable true
		 */
		pageSizeLabel: {schema: {type: 'string'}},
		/**
		 * Custom label to display in place of the current range information.
		 * @type {string}
		 * @uib.translatable true
		 */
		rangeLabel: {schema: {type: 'string'}}
	},
	initialState: {
		/**
		 * Keeps track of current tooltip of page buttons
		 */
		currentTooltip: {ref: null, content: '', opened: false}
	},
	actionHandlers: {
		...createTooltipActionHandler(hideTooltip),
		[COMPONENT_DISCONNECTED]: ({action}) => {
			const componentId = action.meta.id;
			unschedule(componentId);
		},
		'NOW_BUTTON_ICONIC#CLICKED'({action, dispatch}) {
			dispatch(() => ({
				type: 'NOW_PAGINATION_CONTROL#SELECTED_PAGE_SET',
				payload: {
					value: action.payload.page // this is the value that was in append-to-payload above
				}
			}));
		},
		'NOW_DROPDOWN#SELECTED_ITEMS_SET': ({dispatch, action}) => {
			dispatch(() => {
				return {
					type: 'NOW_PAGINATION_CONTROL#SELECTED_PAGE_SIZE_SET',
					payload: {value: Number(action.payload.value[0])}
				};
			});
			dispatch(() => {
				return {
					type: 'NOW_PAGINATION_CONTROL#SELECTED_PAGE_SET',
					payload: {value: 0}
				};
			});
		}
	},
	dispatches: {
		/**
		 * Action dispatched when a page is selected, or when pagination controls are selected.
		 * Payload includea the page the user wants to go to.
		 * @type {{value: number}}
		 * @uib.description Dispatched when a page is selected, or when pagination controls are selected
		 */
		'NOW_PAGINATION_CONTROL#SELECTED_PAGE_SET': {
			schema: {
				type: 'object',
				properties: {
					value: {type: 'number'}
				}
			}
		}
	},
	behaviors: [{behavior: focusBehavior}],
	styles,
	view
});
